.container > li {
  position: absolute;
  z-index: -1;
  pointer-events: none;
  top: 0px;
  width: 0;
  height: 100%;
  border: 1px solid rgba(186, 186, 186, 0.4);
  filter: blur(0.5px);
}

.line1 {
  margin-left: 125px;

  @media screen and (min-width: 1024px) {
    margin-left: 231px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 253px;
  }
}

.line2 {
  margin-left: 259px;

  @media screen and (min-width: 1024px) {
    margin-left: 465px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 555px;
  }
}

.line3 {
  margin-left: -100px;

  @media screen and (min-width: 1024px) {
    margin-left: 699px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 857px;
  }
}
