
.backdrop-modal-room{
  width: 100%;
    height: 100%;
    background-color: rgb(96, 91, 91);
    z-index: 9;
    position: fixed;
    top: 0;
    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    overflow: scroll;
}
 

.backdrope-modal-price {
  width: 100%;
  height: 100%;
  background-color: #fffdee;
  position: fixed;
  top: 0;

}
.backdrope-modal-room.is-hidden {
    opacity: 0;
    pointer-events: none;
  }

.modal-room {

    box-sizing: border-box;
    width: 414px;
    min-height: 780px;
    padding: 60px 15px 60px 15px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid red;
    border-radius: 2px;
    background-color: $background-light-lemon;
    @include tablet {
        border: 1px solid blue;
        width: 712px;
        min-height: 638px;
        padding: 60px;

    }
    @include desktop {
        border: 1px solid green;
        

    }
}
.modal-room-close {
    width: 35px;
    height: 35px;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    border-color: transparent;
    position: absolute;
    right: 16px;
    top: 16px;  
}
.modal-room-close-icon {
    fill: rgba(128, 128, 128, 1)
}
.modal-room-title {
