.modal-room-contacts {
  width: 384px;
  display: block;
  &:not(:last-child) {
    margin-bottom: 20px;
  }
  @include tablet {
    &:not(:last-child) {
      margin-bottom: 0px;
    }
  }
}
.modal-room-input {
  box-sizing: border-box;
  width: 100%;
  height: 54px;
  border: 1px solid $brown-text-color;
  padding-left: 12px;
  background-color: $background-light-lemon;
  font-family: $times-font;
  font-weight: 700;
  font-size: 24px;
  line-height: calc(28 / 24);
  letter-spacing: 0.05em;
  color: #808080;
  margin-bottom: 8px;
  outline: transparent;
  transition: border-color $main-transition-duration
    $main-transtition-timing-function;
  &:hover,
  &:focus {
    border-color: $brown-text-color;
  }

  &::placeholder {
    font-family: $times-font;
    font-weight: 700;
    font-size: 20px;
    line-height: calc(23 / 20);
    letter-spacing: 0.05em;
    color: #808080;
    @include tablet {
      font-size: 24px;
      line-height: calc(28 / 24);
    }
  }
  @include tablet {
    height: 64px;
  }
}

.modal-price-input:not(:focus):not(:placeholder-shown):valid {
  border-color: #c79002;
}
.modal-price-input:not(:focus):not(:placeholder-shown):invalid {
  border-color: rgb(217, 14, 45);
}

